/**
* PARALLAX
*/

.parallax-container {
  overflow: hidden;
  width: 100%;
  height: 100vh; }

.parallax-element {
  position: absolute; }

/**
* BACKGROUND
*/

.background {
  pointer-events: none;
  position: relative;
  top: -100px;
  left: -200px;
  width: calc(100vw + 400px);
  height: calc(100vh + 200px);
  background-image: url("/static/img/piscina-black.png");
  background-size: cover;
  background-position: 0 0; }

/**
* CHART
*/

.chart {
  display: block;
  margin: 0 auto;
  min-width: 100px;
  min-height: 100px;
  margin-left: -200px; }

.chart__wrap {
    overflow: auto; }

.chart__wrap.is-zoomed .resetzoom {
      opacity: 1; }

.chart__ygroup .year {
    stroke: #fff;
    stroke-width: 1px;
    fill: transparent;
    pointer-events: none;
    -webkit-transition: r 1000ms 300ms ease-in-out, opacity 200ms ease;
    transition: r 1000ms 300ms ease-in-out, opacity 200ms ease;
    stroke-dasharray: 6000px;
    stroke-dashoffset: 100%;
    opacity: 0.4; }

.chart__ygroup .year__hover {
      stroke: transparent;
      stroke-width: 16px;
      fill: transparent;
      pointer-events: stroke;
      -webkit-transition: r 1000ms 300ms ease-in-out;
      transition: r 1000ms 300ms ease-in-out; }

.chart__ygroup .year__title {
      display: none;
      fill: white;
      text-anchor: middle;
      font-size: 32px;
      font-family: 'dharma-gothic-m', 'webfontregular', Helvetica, Arial, sans-serif; }

.chart__ygroup .item {
    cursor: pointer;
    -webkit-transition: opacity 200ms ease 100ms;
    transition: opacity 200ms ease 100ms; }

.chart__ygroup .item__dot {
      opacity: 0.7;
      fill: white;
      pointer-events: none;
      stroke: none; }

.chart__ygroup .item__circle {
      stroke: white;
      stroke-width: 1px;
      opacity: 0;
      fill: #050505; }

.chart__ygroup .item__line {
      stroke: white;
      display: none;
      fill: transparent; }

.chart__ygroup .item__text {
      fill: white;
      display: none;
      font-size: 18px; }

.chart__ygroup .item:hover .item__line {
      display: block; }

.chart__ygroup .item:hover .item__text {
      display: block; }

.chart__ygroup .item--shortaudio .item__dot {
      fill: #a74c42; }

.chart__ygroup .item--shortaudio .item__circle {
      stroke: #a74c42; }

.chart__ygroup .item--goodaudio .item__dot {
      fill: red; }

.chart__ygroup .item--goodaudio .item__circle {
      stroke: red; }

.chart__ygroup:hover .year {
    opacity: 1; }

.chart__ygroup:hover .year__title {
      display: block; }

.chart__ygroup:hover .item__dot {
    opacity: 1; }

.chart__ygroup:hover .item__circle {
    opacity: 1; }

@media screen and (min-width: 700px) {
  .chart__metawrap {
    display: block;
    pointer-events: all; }
  .chart__wrap {
    height: 100vh;
    overflow: hidden; } }

/**
* ANIMATIONS
*/

@-webkit-keyframes rotation {
  0% {
    stroke-dasharray: 0;
    stroke-dashoffset: 100%; }
  100% {
    stroke-dasharray: 6000px;
    stroke-dashoffset: 0; } }

@keyframes rotation {
  0% {
    stroke-dasharray: 0;
    stroke-dashoffset: 100%; }
  100% {
    stroke-dasharray: 6000px;
    stroke-dashoffset: 0; } }
